<template>
    <el-row>
      <el-steps :active="active" finish-status="success">
        <el-step title="初始化表单"></el-step>
        <el-step title="股本与股东"></el-step>
        <el-step title="首任秘書"></el-step>
        <el-step title="首任董事" :status="forthStatus"></el-step>
      </el-steps>

      <!-- page1 -->
      <el-form v-show="active == 0" class="ncc1-page" ref="formPage0" :model="form" :rules="rules" label-width="150px">
        <el-row class="nnc1-module-title">A.填写公司基础信息 Basic Information Company</el-row>
        <el-row class="form-item-container">
          <el-form-item label="法定文件版本">
            <el-radio-group v-model="form.language">
              <el-radio label="cn">繁体中文　Traditional</el-radio>
              <el-radio label="en">英文　English</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="公司英文名稱:" prop="companyNameEn">
            <el-input v-model="form.companyNameEn" placeholder="公司英文名称只能由字母以及符号和空格组成并以 LIMITED 结尾"></el-input>
          </el-form-item>
          <el-form-item label="公司中文名稱:" prop="companyNameCn">
            <el-input v-model="form.companyNameCn" placeholder="公司中文名称只能由数字或中文汉字以及符号与空格并以有限公司结尾"></el-input>
          </el-form-item>
          <el-form-item label="註冊辦事處地址:" prop="companyRegistAddr">
            <el-input v-model="form.companyRegistAddr" placeholder="公司所使用的註冊辦事處的建議地址"></el-input>
          </el-form-item>
        </el-row>
        <el-row class="nnc1-module-title">B.提交人的资料　Presentor’s Reference</el-row>
        <el-row class="form-item-container">
          <el-form-item label="提交人信息">
            <el-radio-group v-model="submitUser.userInfo" @change="changeSubmitUser">
              <el-radio label="defaultUser">默认</el-radio>
              <el-radio label="selfUser">自定义</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="提交人名稱:" prop="submitUserName">
            <el-input v-model="form.submitUserName" placeholder="提交人的中文名称"></el-input>
          </el-form-item>
          <el-form-item label="提交人地址:" prop="submitUserAddr">
            <el-input v-model="form.submitUserAddr" placeholder="提交人的地址"></el-input>
          </el-form-item>
          <el-row>
            <el-col :span="12">
              <el-form-item label="提交人電話:" prop="submitUserPhone">
                <el-input v-model="form.submitUserPhone" placeholder="提交人電話"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="提交人傳真:" prop="submitUserFax">
                <el-input v-model="form.submitUserFax" placeholder="提交人傳真"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="提交人電郵:" prop="submitUserEmail">
              <el-input v-model="form.submitUserEmail" placeholder="提交人電郵" type="email"></el-input>
          </el-form-item>
        </el-row>
      </el-form>

      <!-- page2 -->
      <el-form v-show="active == 1" class="ncc1-page" ref="formPage1" :model="form" :rules="rules" label-width="150px">
        <el-row class="nnc1-module-title">A：股本信息　Share Capital</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="股份的類別:" prop="companyStockType">
                    <el-input v-model="form.companyStockType" placeholder="股份的類別"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="貨幣:" prop="companyStockCurrency">
                    <el-input v-model="form.companyStockCurrency" placeholder="貨幣(HKD)"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-row>
                <el-form-item label="建議發行股份總數:" prop="companyStockTotal">
                    <el-input v-model="form.companyStockTotal" placeholder="建議發行股份總數(数字)"></el-input>
                </el-form-item>
            </el-row>
        </el-row>
        <el-row class="nnc1-module-title">B：创办成员/股东信息　Founder Members</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="中文姓名:" prop="funderNameCn">
                    <el-input v-model="form.funderNameCn" placeholder="必须为中文"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="英文姓名:" prop="funderNameEn">
                    <el-input v-model="form.funderNameEn" placeholder="姓和名必须以'-'分割，如WU-JIANMIN"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item label="地址:" prop="funderAddr">
              <el-input v-model="form.funderAddr" placeholder="联络地址或证件地址"></el-input>
            </el-form-item>
            <el-row>
              <el-col :span="12">
                <el-form-item label="國家／地區:" prop="funderCountry">
                    <el-input v-model="form.funderCountry" placeholder="國家／地區"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="占股份的数目:" prop="funderStockNum">
                    <el-input v-model="form.funderStockNum" placeholder="小于等于發行股份總數"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item>
              <el-checkbox-group v-model="form.isChairman">
                <el-checkbox name="isChairman">作为董事 (如作为董事该信息将体现在董事栏)</el-checkbox>
              </el-checkbox-group>
            </el-form-item>
        </el-row>
      </el-form>

      <!-- page3 -->
      <el-form v-show="active == 2" class="ncc1-page" ref="formPage2" :model="form" :rules="rules" label-width="150px">
        <el-row class="nnc1-module-title">首任秘书　First Secretary</el-row>
        <el-row class="form-item-container">
            <el-row>
              <el-col :span="12">
                <el-form-item label="公司編號:" prop="companyNumber">
                    <el-input v-model="form.companyNumber" placeholder="公司編號"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="12">
                <el-form-item label="公司邮件:" prop="companyEmail">
                    <el-input v-model="form.companyEmail" placeholder="公司邮件(NIL)"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item label="公司中文名稱:" prop="secretaryNameCn">
                <el-input v-model="form.secretaryNameCn" placeholder="公司中文名稱"></el-input>
            </el-form-item>
            <el-form-item label="公司英文名稱:" prop="secretaryNameEn">
                <el-input v-model="form.secretaryNameEn" placeholder="公司英文名稱"></el-input>
            </el-form-item>
            <el-form-item label="香港地址:" prop="secretaryHKAddr">
                <el-input v-model="form.secretaryHKAddr" placeholder="香港地址"></el-input>
            </el-form-item>
        </el-row>
      </el-form>

      <!-- page4 -->
      <el-form v-show="active == 3" class="ncc1-page" ref="formPage3" :model="form" :rules="rules" label-width="150px">
        <el-row class="nnc1-module-title">首任董事　First Directors</el-row>
        <el-row class="form-item-container">
          <el-row>
            <el-col :span="8">
              <el-form-item label="董事姓名:" prop="chairmanNameCn">
                  <el-input v-model="form.chairmanNameCn" placeholder="中文姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="英文姓名:" prop="chairmanSureName">
                  <el-input v-model="form.chairmanSureName" placeholder="英文姓式"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="8">
              <el-form-item label="英文姓名:" prop="chairmanOtherName">
                  <el-input v-model="form.chairmanOtherName" placeholder="英文名字"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-form-item label="國家／地區:" prop="chairmanCountry">
              <el-input v-model="form.chairmanCountry" placeholder="國家／地區"></el-input>
          </el-form-item>
          <el-form-item label="住址:" prop="chairmanAddr">
              <el-input v-model="form.chairmanAddr" placeholder="联络地址或证件地址"></el-input>
          </el-form-item>
          <el-form-item label="证件类型:">
              <el-radio-group v-model="form.certificateType" @change="changeCertificateType">
                <el-radio label="chinaId">身份證明CHINA ID</el-radio>
                <el-radio label="hkId">身份分證明HongKong ID</el-radio>
                <el-radio label="passport">護照</el-radio>
              </el-radio-group>
          </el-form-item>
          <el-form-item v-show="form.certificateType == 'chinaId'" prop="chairmanChinaId">
              <el-input v-model="form.chairmanChinaId" placeholder="个人董事在中国大陆的身份证号码"></el-input>
          </el-form-item>
          <el-form-item v-show="form.certificateType == 'hkId'" prop="chairmanHKId">
              <el-input v-model="form.chairmanHKId" placeholder="个人董事在香港身份证号码"></el-input>
          </el-form-item>
          <el-row v-show="form.certificateType == 'passport'">
              <el-col :span="12">
                  <el-form-item label="護照簽發國家:" prop="passportCountry">
                    <el-input v-model="form.passportCountry" placeholder="護照簽發國家"></el-input>
                  </el-form-item>
              </el-col>
              <el-col :span="12">
                  <el-form-item label="護照號碼:" prop="passportNumber">
                    <el-input v-model="form.passportNumber" placeholder="護照號碼"></el-input>
                  </el-form-item>
              </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="姓名簽署:" prop="funderNameSigned">
                  <el-input v-model="form.funderNameSigned" placeholder="姓名簽署"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="簽署日期:" prop="funderDateSigned">
                <el-date-picker
                  type="date" 
                  placeholder="选择日期"
                  v-model="form.funderDateSigned"
                  style="width: 100%;"
                  format="dd-MM-yyyy"
                  value-format="dd-MM-yyyy"
                ></el-date-picker>
              </el-form-item>
            </el-col>
          </el-row>
        </el-row>
      </el-form>

      <el-row class="btn-row">
        <el-button @click="prePage" v-show="active !== 0">上一步</el-button>
        <el-button @click="nextPage" v-show="active !== 3">下一步</el-button>
        <el-button @click="nextPage" v-show="active == 3">提交</el-button>
      </el-row>
    </el-row>
</template>

<script>
import defaultInfo from '@/config/defaultInfo'
import { ProduceWord } from '@/api'

export default {
  data() {
    const validateFunderNameEn = (rule, value, cb) => {
        const arr = value.split('-')
        if (arr[0] && arr[1]) {
          cb()
        } else {
          cb(new Error('必须以-隔开'))
        }
    }

    return {
      active: 0,
      forthStatus: 'wait',
      form: {
        language: 'cn',
        companyNameEn: '',
        companyNameCn: '',
        companyRegistAddr: '',
        submitUserName: defaultInfo.userName,
        submitUserAddr: defaultInfo.userAddr,
        submitUserPhone: defaultInfo.userPhone,
        submitUserFax: defaultInfo.userFax,
        submitUserEmail: defaultInfo.userEmail,
        companyStockType: defaultInfo.companyStockType,
        companyStockCurrency: '',
        companyStockTotal: '',
        funderNameCn: '',
        funderNameEn: '',
        funderAddr: '',

        // 是否作为懂事
        isChairman: false,

        companyNumber: '',
        companyEmail: '',
        secretaryNameCn: '',
        secretaryNameEn: '',
        secretaryHKAddr: '',

        chairmanNameCn: '',
        chairmanSureName: '',
        chairmanOtherName: '',
        chairmanCountry: '',
        chairmanAddr: '',

        funderNameSigned: '',
        funderDateSigned: '',

        // 证件类型
        certificateType: 'chinaId',
        chairmanChinaId: '',
        chairmanHKId: 'NIL',
        passportCountry: 'NIL',
        passportNumber: 'NIL'
      },
      submitUser: {
        userInfo: 'defaultUser'
      },
      rules: {
        companyNameEn: [
          { required: true, message: '请输入公司英文名稱' }
        ],
        companyNameCn: [
          { required: true, message: '请输入公司中文名稱' }
        ],
        companyRegistAddr: [
          { required: true, message: '请输入註冊辦事處地址' }
        ],
        submitUserName: [
          { required: true, message: '请输入提交人名稱' }
        ],
        submitUserAddr: [
          { required: true, message: '请输入提交人地址' }
        ],
        submitUserPhone: [
          { required: true, message: '请输入提交人電話' }
        ],
        submitUserFax: [
          { required: true, message: '请输入提交人傳真' }
        ], 
        submitUserEmail: [
          { required: true, message: '请输入提交人電郵' }
        ],

        companyStockType: [
          { required: true, message: '请输入股份的類別' }
        ],
        companyStockCurrency: [
          { required: true, message: '请输入貨幣' }
        ],
        companyStockTotal: [
          { required: true, message: '请输入建議發行的股份總數' }
        ],
        funderNameCn: [
          { required: true, message: '请输入中文姓名' }
        ],
        funderNameEn: [
          { required: true, message: '请输入英文姓名' },
          { validator: validateFunderNameEn }
        ],
        funderAddr: [
          { required: true, message: '请输入地址' }
        ],
        funderCountry: [
          { required: true, message: '请输入國家／地區' }
        ],
        funderStockNum: [
          { required: true, message: '请输入占股份的数目' }
        ],

        companyNumber: [
          { required: true, message: '请输入公司編號' }
        ],
        companyEmail: [
          { required: true, message: '请输入公司邮件' }
        ],
        secretaryNameCn: [
          { required: true, message: '公司中文名稱' }
        ],
        secretaryNameEn: [
          { required: true, message: '公司英文名稱' }
        ],
        secretaryHKAddr: [
          { required: true, message: '香港地址' }
        ],

        chairmanNameCn: [
          { required: true, message: '请输入董事姓名' }
        ],
        chairmanSureName: [
          { required: true, message: '请输入英文姓式' }
        ],
        chairmanOtherName: [
          { required: true, message: '请输入英文名字' }
        ],
        chairmanCountry: [
          { required: true, message: '请输入國家／地區' }
        ],
        chairmanAddr: [
          { required: true, message: '请输入联络地址或证件地址' }
        ],
        chairmanChinaId: [
          { required: true, message: '请输入中国大陆的身份证号码' },
          // { validator: validateRequire }
        ],
        chairmanHKId: [
          { required: true, message: '请输入香港身份证号码' },
          // { validator: validateRequire }
        ],
        passportCountry: [
          { required: true, message: '请输入護照簽發國家' },
          // { validator: validateRequire }
        ],
        passportNumber: [
          { required: true, message: '请输入護照號碼' },
          // { validator: validateRequire }
        ],
        funderNameSigned: [
          { required: true, message: '请输入姓名簽署' }
        ],
        funderDateSigned: [
          { required: true, message: '请输入选择日期' }
        ]
      }
    }
  },

  methods: {
    changeSubmitUser(value) {
      // 切换到自定义提交人
      if (value == 'selfUser') {
        this.form = {
          ...this.form,
          submitUserName: '',
          submitUserAddr: '',
          submitUserPhone: '',
          submitUserFax: '',
          submitUserEmail: '',
          companyStockType: '',
        }
      } else {
        this.form = {
          ...this.form,
          submitUserName: defaultInfo.userName,
          submitUserAddr: defaultInfo.userAddr,
          submitUserPhone: defaultInfo.userPhone,
          submitUserFax: defaultInfo.userFax,
          submitUserEmail: defaultInfo.userEmail,
          companyStockType: defaultInfo.companyStockType
        }
      }
    },

    changeCertificateType(value) {
      this.form = {
        ...this.form,
        chairmanChinaId: value == 'chinaId' ? '' : 'NIL',
        chairmanHKId: value == 'hkId' ? '' : 'NIL',
        passportCountry: value == 'passport' ? '' : 'NIL',
        passportNumber: value == 'passport' ? '' : 'NIL'
      }
    },

    prePage() {
      if (this.active > 0) {
        this.active = this.active - 1
      }    
      this.forthStatus = 'wait'
    },

    nextPage() {
      const formObj = {
        0: 'formPage0',
        1: 'formPage1',
        2: 'formPage2',
        3: 'formPage3'
      }
      this.$refs[formObj[this.active]].validate(valid => {
        if (valid) {
          if (this.active < 3) {
            // 如果第2页的作为董事勾选
            if (this.active == 1 && this.form.isChairman) {
              const { funderNameCn, funderAddr, funderCountry } = this.form
              const [chairmanSureName, chairmanOtherName] = this.form.funderNameEn.split('-')
              this.form = {
                ...this.form,
                chairmanNameCn: funderNameCn,
                chairmanSureName,
                chairmanOtherName,
                chairmanAddr: funderAddr,
                chairmanCountry: funderCountry
              }
            }
            this.active = this.active + 1
          } else {
            const params = Object.assign({}, this.form)
            delete params.certificateType
            delete params.isChairman
            delete params.language
            params.funderNameEn = params.funderNameEn.split('-').join('')
            // console.log(params)
            this.produceWord('NNC1', params)
          }
        } else {
          return false
        }
      })
    },

    produceWord(wordParams, wordName) {
      const data = {
        docxName: wordName,
        docxParams: wordParams
      }

      ProduceWord(data).then(() => {
          this.forthStatus = 'success'
          const url = `/output/${wordName}`
          const fileName = `${wordName}.docx`
          this.$alert(`<a href=${url} download=${fileName}>${fileName}</a>`, 'WORD 下载，点击下载', {
              dangerouslyUseHTMLString: true
          })
      }).catch(({ code, msg }) => {
          this.$message({
              message: msg,
              type: 'error',
              duration: 2000
          })
      })
    }
  }
}
</script>

<style scoped>
.ncc1-page {
  margin-top: 20px;
}

.form-item-container {
  width: 80%;
  margin: 0 auto;
}

.nnc1-module-title {
  padding: 10px 0;
  border-bottom: 1px solid #333;
  margin-bottom: 16px;
}

.btn-row {
  width: 80%;
  margin: 0 auto;
  text-align: right;
}
</style>





